import { app } from './app'

app.$mount('#app')

// eslint-disable-next-line no-undef
var ws = new WebSocket('ws://' + location.host)
ws.onopen = function () {
  console.log('Successfully connect WebSocket')

  ws.send(JSON.stringify({ event: 'monitorGateway' }))
}

ws.onmessage = (message) => {
  console.log(message.data)
}



